import React, { useEffect, useState } from "react"
import styled from "@emotion/styled"
import { css } from "@emotion/core"
import { isMobile } from "react-device-detect"
import createWaflashModule, { initDragAndDrop } from "../../static/wasm/waflash.mjs"

const Waflash = (props) => {
    if (props.disable) return(<div></div>)

    useEffect(() => {
        const src = props.src && props.src.publicURL ? window.location.origin + props.src.publicURL : '';
        const mode = isMobile ? 'webgl' : 'auto';
        let waflash = {
            arguments: [ src, mode ],
            preRun: [],
            postRun: [],
            locateFile(path, prefix) {
                return "/wasm/" + path;
            },
            print(text) {
                //if (arguments.length > 1) text = Array.prototype.slice.call(arguments).join(' ');
                console.log(text);
            },
            printErr(text) {
                //if (arguments.length > 1) text = Array.prototype.slice.call(arguments).join(' ');
                console.error(text);
            },
            canvas: (function () {
                const canvas = document.getElementById("canvas");
                // As a default initial behavior, pop up an alert when webgl context is lost. To make your
                // application robust, you may want to override this behavior before shipping!
                // See http://www.khronos.org/registry/webgl/specs/latest/1.0/#5.15.2
                canvas.addEventListener("webglcontextlost", function (e) { alert('WebGL context lost. You will need to reload the page.'); e.preventDefault(); }, false);
                return canvas;
            })(),
            // monitorRunDependencies: (() => {
            //     let totalDependencies = 0;
            //     return left => {
            //         totalDependencies = Math.max(totalDependencies, left);
            //         waflash.setStatus(left ? 'Preparing... (' + (totalDependencies-left) + '/' + totalDependencies + ')' : 'All downloads complete.');
            //     }
            // })(),
            statusElement: (() => document.getElementById('waflashStatus'))(),
            setStatus: (text) => {
                if (!text) return;

                text = text.replace(/Downloading data\.\.\. \((\d+)\/(\d+)\)/, (match, receivedBytes, totalBytes) => {
                    return 'Downloading data... ' + Math.floor(parseInt(receivedBytes)/parseInt(totalBytes)*100) + '%';
                });

                console.log('WAFLASH> ' + text);
                waflash.statusElement.innerHTML = text;
                waflash.showStatus();
            },
            showStatus() { // helper function
                waflash.statusElement.style.display = 'block';
            },
            hideStatus() { // helper function
                waflash.statusElement.style.display = 'none';
            },
            unload() {},
        };
        
        // window.onerror = (message, url, line) => {
        //     console.log("WAFLASH> window.onerror; message=", message, ", url=", url, ", line=", line);
        //     waflash.setStatus(message); 
        // };

        waflash.setStatus("Prepairing...");

        createWaflashModule(waflash).then((o) => {
            console.log("WAFLASH> Waflash module created!");
            waflash.hideStatus();
            if (!src) initDragAndDrop(waflash);
        });
     
        return () => {
            console.log("WAFLASH> Waflash component will unmount!");
            waflash.unload();
            waflash = null;
        };
    })

    const waflashCanvasStyle = css`
        padding-right: 0;
        margin-left: auto;
        margin-right: auto;
        display: block;
        border: 0px none;
        background-color: black;
        width: ${props.width || '100%'};
        height: ${props.height || '480px'};
        @media (max-width: 640px) {
            width: 100%;
            height: 75vw;
        }
        @media (max-width: 319px) {
            width: 240px;
            height: 180px;
        }
        @media (orientation:landscape) and (max-height:480px) {
            width: 100%;
            height: 75vh;
        }
    `

    const waflashStatusStyle = css`
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 70%;
        color: white;
        margin: 0px auto;
        padding: 20px;
        font-size: 1.2rem;
        text-align: center;
        background-color: rgba(0,0,0,0.1);
        display: none;
    `

    return (
        <div id="waflashContainer" style={{ position: 'relative', border: `1px solid black` }}>
            <canvas id="canvas" css={waflashCanvasStyle} onContextMenu={(e) => e.preventDefault()} tabIndex={-1}></canvas>
            <div id="waflashStatus" css={waflashStatusStyle}></div>
        </div>
    )
}

export default Waflash
